module.exports = "<div><section class=start><div class=center-vertically><h1 class=typo__h1><img src=" + require("./assets/logo.png") + " class=logo />Vue-multiselect</h1><h3 class=typo__h3>The most complete selecting solution for <a href=http://vuejs.org target=_BLANK class=typo__link>Vue.js</a></h3><div class=\"grid__row grid__row--centered\"><div class=\"grid__column grid__unit--md-6\"><div class=multiselect-example__container><multiselect :options=options :selected=selected :multiple=multiple :searchable=searchable :placeholder=placeholder :on-change=afterChange :show-labels=true :limit=3 select-label=\"Enter to select\"></multiselect><div class=\"grid__row start__list\"><div class=\"grid__column grid__unit--md-6 list\"><ul class=list__ul><li class=typo__li>Single / multi select</li><li class=typo__li>Dropdowns</li><li class=typo__li>Searchable</li><li class=typo__li>Custom components with Mixins</li></ul></div><div class=\"grid__column grid__unit--md-6 list\"><ul class=list__ul><li class=typo__li>Vuex support</li><li class=typo__li>Async options</li><li class=typo__li>Configurable</li><li class=typo__li>+90% test coverage</li><li class=typo__li>No dependencies</li></ul></div></div></div></div></div><div class=\"grid__row grid__row--centered\"><div class=\"grid__column utils--center\"><a href=https://github.com/monterail/vue-multiselect target=_BLANK class=\"button button--large button--secondary button--github\">View on GitHub</a><a href=#getting-started class=\"button button--large\">Getting started & examples</a></div></div></div></section><div class=grid__row><div class=grid__column><section id=getting-started class=docs><h1 class=typo__h1>Getting started</h1><hr class=typo__hr /><div class=grid__row><div class=\"grid__column grid__unit--md-6\"><h2 class=typo__h2>Installation</h2><pre class=language-bash><code>npm install vue-multiselect --save\n</code></pre><h2 class=typo__h2>Package content</h2><p class=typo__p>The package consist of of multiple files:<strong> Multiselect.vue</strong> which includes the template and styling of the default component. This component extends 2 additional mixins:<strong> multiselectMixin.js</strong> and<strong> pointerMixin.js</strong> , which contain the logic behind the multiselect. This way you can actually use the multiselect logic with your own template and styling.</p></div><div class=\"grid__column grid__unit--md-6\"><h2 class=typo__h2>Basic usage</h2><pre class=language-jade><code>multiselect(\n  :selected.sync=\"selected\",\n  :options=\"options\"\n)</code></pre><pre class=language-javascript><code>import Multiselect from 'vue-multiselect'\nexport default {\n  components: { Multiselect },\n  data () {\n    return {\n      selected: null,\n      options: ['list', 'of', 'options']\n    }\n  }\n}\n</code></pre></div></div></section></div></div><div class=grid__row><div class=grid__column><section class=docs><h1 class=typo__h1>Examples</h1><hr class=typo__hr /><h2 class=typo__h2>Single select dropdown (primitive value)</h2><p class=typo__p>Disabled all highlight label text.</p><div class=grid__row><div class=\"grid__column grid__unit--md-5\"><label class=typo__label>Single select / dropdown</label><multiselect :options=options :selected.sync=valuePrimitive :multiple=false :searchable=false :close-on-select=false :show-labels=false placeholder=\"Select one\" label=name key=name></multiselect><pre class=language-json><code>{{ valuePrimitive | json }}\n</code></pre></div><div class=\"grid__column grid__unit--md-7\"><label class=typo__label>Code sample</label><pre class=language-jade><code>multiselect(\n  :options=\"options\",\n  :selected.sync=\"valuePrimitive\",\n  :multiple=\"false\",\n  :searchable=\"false\",\n  :close-on-select=\"false\",\n  :show-labels=\"false\"\n  placeholder=\"Select one\"\n  label=\"name\"\n  key=\"name\"\n)\n</code></pre></div></div><h2 class=typo__h2>Single select dropdown (object)</h2><p class=typo__p>Can’t become empty. Showing custom deselect label.</p><p class=typo__p>The selected value is referencing the same object as the next example, to show that dynamic preselection works.</p><div class=grid__row><div class=\"grid__column grid__unit--md-5\"><label class=typo__label>Single select / dropdown</label><multiselect :options=source :selected.sync=value :multiple=false :searchable=false :close-on-select=false :allow-empty=false deselect-label=\"Can’t remove this value\" key=name label=name placeholder=\"Select one\"></multiselect><pre class=language-json><code>{{ value | json }}\n</code></pre></div><div class=\"grid__column grid__unit--md-7\"><label class=typo__label>Code sample</label><pre class=language-jade><code>multiselect(\n  :options=\"source\",\n  :selected.sync=\"value\",\n  :multiple=\"false\",\n  :searchable=\"false\",\n  :close-on-select=\"false\",\n  :allow-empty=\"false\",\n  deselect-label=\"Can’t remove this value\"\n  key=\"name\"\n  label=\"name\"\n  placeholder=\"Select one\"\n)\n</code></pre></div></div><hr class=typo__hr /><h2 class=typo__h2>Single select with search</h2><p class=typo__p>The selected value is referencing the same object as the previous example, to show that dynamic preselection works.</p><div class=grid__row><div class=\"grid__column grid__unit--md-5\"><label class=typo__label>Simple select / dropdown</label><multiselect :options=source :selected.sync=value :multiple=false :searchable=true placeholder=\"Select one\" label=name key=name></multiselect><pre class=language-json><code>{{ value | json }}\n</code></pre></div><div class=\"grid__column grid__unit--md-7\"><label class=typo__label>Code sample</label><pre class=language-jade><code>multiselect(\n  :options=\"source\",\n  :selected.sync=\"value\",\n  :multiple=\"false\",\n  :searchable=\"true\",\n  placeholder=\"Select one\",\n  label=\"name\",\n  key=\"name\"\n)\n</code></pre></div></div><hr class=typo__hr /><h2 class=typo__h2>Multiple select with search</h2><p class=typo__p>Limit visible results to 2.</p><div class=grid__row><div class=\"grid__column grid__unit--md-5\"><label class=typo__label>Simple select / dropdown</label><multiselect :options=source :selected.sync=multiValue :multiple=true :searchable=true :close-on-select=true :clear-on-select=false :limit=2 placeholder=\"Pick some\" label=name key=name></multiselect><pre class=language-json><code>{{ multiValue | json }}\n</code></pre></div><div class=\"grid__column grid__unit--md-7\"><label class=typo__label>Code sample</label><pre class=language-jade><code>multiselect(\n  :options=\"source\",\n  :selected.sync=\"multiValue\",\n  :multiple=\"true\",\n  :searchable=\"true\",\n  :close-on-select=\"true\",\n  :clear-on-select=\"false\",\n  placeholder=\"Pick some\"\n  label=\"name\",\n  key=\"name\"\n)\n</code></pre></div></div><hr class=typo__hr /><h2 class=typo__h2>Asynchronous dropdown</h2><p class=typo__p>Changing the search query calls the <kbd><code>:on-search-change=\"\"</code></kbd> callback function, passing the search query as param. It also sets the <kbd><code>loading = true</code></kbd>.</p><p class=typo__p>To disable <kbd><code>loading</code></kbd> options have to be updated.</p><div class=grid__row><div class=\"grid__column grid__unit--md-5\"><label class=typo__label>Async multiselect</label><multiselect :options=countries :selected.sync=selectedCountries :multiple=multiple :searchable=searchable placeholder=\"Type to search\" :on-search-change=asyncFind :clear-on-select=false label=name key=code><span slot=noResult>Oops! No elements found. Consider changing the search query.</span></multiselect><pre class=language-json><code>{{ selectedCountries | json }}\n</code></pre></div><div class=\"grid__column grid__unit--md-7\"><label class=typo__label>Code sample</label><pre class=language-jade><code>multiselect(\n  :options=\"countries\",\n  :selected.sync=\"selectedCountries\",\n  :multiple=\"multiple\",\n  :searchable=\"searchable\",\n  placeholder=\"Type to search\",\n  :on-search-change=\"asyncFind\",\n  :clear-on-select=\"false\",\n  label=\"name\"\n  key=\"code\"\n)\n  span(slot=\"noResult\").\n    Oops! No elements found. Consider changing the search query.\n</code></pre></div></div><hr class=typo__hr /><h2 class=typo__h2>on-change props</h2><p class=typo__p>If set triggers the passed callback function after value changes, passing the new value as param.</p><p class=typo__p>For <strong>Vuex</strong> integration simply remove <kbd><code>:reset-after=\"true\"</code></kbd> prop.</p><p class=typo__p>May also act as dispatcher for different actions, like in this example.</p><div class=grid__row><div class=\"grid__column grid__unit--md-5\"><label class=typo__label>Open console to see logs.</label><multiselect :options=actions :selected=action :multiple=false :searchable=false placeholder=\"Pick action\" :reset-after=true :on-change=dispatchAction></multiselect></div><div class=\"grid__column grid__unit--md-7\"><label class=typo__label>Code sample</label><pre class=language-jade><code>multiselect(\n  :options=\"actions\",\n  :selected=\"action\",\n  :multiple=\"false\",\n  :searchable=\"false\",\n  placeholder=\"Pick action\",\n  :reset-after=\"true\",\n  :on-change=\"dispatchAction\"\n)\n</code></pre></div></div><hr class=typo__hr /><h2 class=typo__h2>Custom configuration</h2><p class=typo__p>Can’t remove the last value.</p><p class=typo__p>Hides already selected options.</p><p class=typo__p>Shows error when touched, but nothing is selected.</p><div class=grid__row><div class=\"grid__column grid__unit--md-5\"><div :class=\"{ 'invalid': isInvalid }\"><label class=typo__label>Must have at least one value</label><multiselect :options=options :selected.sync=exampleValue6 :multiple=true :searchable=true :allow-empty=false :hide-selected=true :touched.sync=isTouched :max-height=400 placeholder=\"Pick at least one\"></multiselect></div></div><div class=\"grid__column grid__unit--md-7\"><label class=typo__label>Code sample</label><pre class=language-jade><code>multiselect(\n  :options=\"options\",\n  :selected.sync=\"exampleValue6\",\n  :multiple=\"true\",\n  :searchable=\"true\",\n  :allow-empty=\"false\",\n  :hide-selected=\"true\",\n  :touched.sync=\"isTouched\",\n  :max-height=\"400\"\n  placeholder=\"Pick at least one\"\n)\n</code></pre></div></div><hr class=typo__hr /></section><section class=docs><h1 class=typo__h1>Config</h1><hr class=typo__hr /><h2 class=typo__h2>Props</h2><div class=grid__row><div class=grid__column><pre class=language-javascript><code>// multiselectMixin.js\n\nprops: {\n  /**\n   * Array of available options: Objects, Strings or Integers.\n   * If array of objects, visible label will default to option.label.\n   * If `labal` prop is passed, label will equal option['label']\n   * @type {Array}\n   */\n  options: {\n    type: Array,\n    required: true,\n    /* istanbul ignore next  */\n    default () {\n      return []\n    }\n  },\n  /**\n   * Equivalent to the `multiple` attribute on a select input.\n   * @default false\n   * @type {Boolean}\n   */\n  multiple: {\n    type: Boolean,\n    default: false\n  },\n  /**\n   * Required. Presets the selected options. Add `.sync` to\n   * update parent value. If this.onChange callback is present,\n   * this will not update. In that case, the parent is responsible\n   * for updating this value.\n   * @type {Object||Array||String||Integer}\n   */\n  selected: {\n    required: true\n  },\n  /**\n   * Key to compare objects\n   * @default 'id'\n   * @type {String}\n   */\n  key: {\n    type: String,\n    default: 'id'\n  },\n  /**\n   * Label to look for in option Object\n   * @default 'label'\n   * @type {String}\n   */\n  label: {\n    type: String,\n    default: 'label'\n  },\n  /**\n   * Label to look for in option Object\n   * @default 'label'\n   * @type {String}\n   */\n  limit: {\n    type: Number,\n    default: 99999\n  },\n  /**\n   * Enable/disable search in options\n   * @default true\n   * @type {Boolean}\n   */\n  searchable: {\n    type: Boolean,\n    default: true\n  },\n  /**\n   * Clear the search input after select()\n   * @default true\n   * @type {Boolean}\n   */\n  clearOnSelect: {\n    type: Boolean,\n    default: true\n  },\n  /**\n   * Hide already selected options\n   * @default false\n   * @type {Boolean}\n   */\n  hideSelected: {\n    type: Boolean,\n    default: false\n  },\n  /**\n   * Equivalent to the placeholder attribute on a select input.\n   * @default 'Select option'\n   * @type {String}\n   */\n  placeholder: {\n    type: String,\n    default: 'Select option'\n  },\n  /**\n   * Sets maxHeight style value of the dropdown\n   * @default 300\n   * @type {Integer}\n   */\n  maxHeight: {\n    type: Number,\n    default: 300\n  },\n  /**\n   * Allow to remove all selected values\n   * @default true\n   * @type {Boolean}\n   */\n  allowEmpty: {\n    type: Boolean,\n    default: true\n  },\n  /**\n   * Callback function to call after this.value changes\n   * @callback onChange\n   * @default false\n   * @param {Array||Object||String||Integer} Current this.value\n   * @param {Integer} $index of current selection\n   * @type {Function}\n   */\n  onChange: {\n    type: Function,\n    default: false\n  },\n  /**\n   * Callback function to call after this.search changes\n   * @callback onSearchChange\n   * @default false\n   * @param {String} Pass current search String\n   * @type {Function}\n   */\n  onSearchChange: {\n    type: Function,\n    default: false\n  },\n  /**\n   * Value that indicates if the dropdown has been used.\n   * Useful for validation.\n   * @default false\n   * @type {Boolean}\n   */\n  touched: {\n    type: Boolean,\n    default: false\n  },\n  /**\n   * Reset this.value, this.search, this.selected after this.value changes.\n   * Useful if want to create a stateless dropdown, that fires the this.onChange\n   * callback function with different params.\n   * @default false\n   * @type {Boolean}\n   */\n  resetAfter: {\n    type: Boolean,\n    default: false\n  },\n  /**\n   * Enable/disable closing after selecting an option\n   * @default true\n   * @type {Boolean}\n   */\n  closeOnSelect: {\n    type: Boolean,\n    default: true\n  }\n}\n\n// pointerMixin.js\n\nprops: {\n  /**\n   * Enable/disable highlighting of the pointed value.\n   * @type {Boolean}\n   * @default true\n   */\n  showPointer: {\n    type: Boolean,\n    default: true\n  }\n}\n\n// Multiselect.vue\n//\nprops: {\n  /**\n   * String to show when pointing to an option\n   * @default 'Press enter to select'\n   * @type {String}\n   */\n  selectLabel: {\n    type: String,\n    default: 'Press enter to select'\n  },\n  /**\n   * String to show next to selected option\n   * @default 'Selected'\n   * @type {String}\n  */\n  selectedLabel: {\n    type: String,\n    default: 'Selected'\n  },\n  /**\n   * String to show when pointing to an alredy selected option\n   * @default 'Press enter to remove'\n   * @type {String}\n  */\n  deselectLabel: {\n    type: String,\n    default: 'Press enter to remove'\n  },\n  /**\n   * Decide whether to show pointer labels\n   * @default true\n   * @type {Boolean}\n  */\n  showLabels: {\n    type: Boolean,\n    default: true\n  }\n}\n</code></pre></div></div></section><section class=utils--center><hr class=typo__hr /><h4 class=typo__h4>Created by Damian Dulisz <strong><a href=https://twitter.com/DamianDulisz target=_BLANK class=typo__link> @DamianDulisz</a></strong></h4><h4 class=typo__h4>With love from <strong><a href=http://monterail.com target=_BLANK class=\"typo__link monterail-link\"> Monterail</a></strong></h4><a href=http://monterail.com target=_BLANK><img src=/static/monterail-logo.png class=monterail-logo /></a></section></div></div></div>";


